<template>
  <div class="schedule" v-on:load-game="doStuff($event)">
    <button @click="incrementDate(-1)"><<</button>
    <kendo-datepicker :value="currentDate" @change="updateDate"></kendo-datepicker>
    <button @click="incrementDate(1)">>></button>
    <div v-show="scheduleData">
        <div class="schedule_data half_screen">
            <pre>{{ JSON.stringify(scheduleData.games, null, 2) }}</pre>
        </div>
        <div class="game_list half_screen">
            <kendo-grid :data-source="scheduleData.games">
                <kendo-grid-column :field="'gamePk'" :title="'GamePk'"></kendo-grid-column>
                <kendo-grid-column :field="'gameDate'" :title="'Game Date/Time'" :template="getDate"></kendo-grid-column>
                <kendo-grid-column :field="'teams.away.team.name'" :title="'Away Team'"></kendo-grid-column>
                <kendo-grid-column :field="'teams.home.team.name'" :title="'Home Team'"></kendo-grid-column>
                <kendo-grid-column :template="getData"></kendo-grid-column>
            </kendo-grid>
        </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios';
import go_to_game_button from '@/components/GoToGameButton.vue';
import Vue from "vue";

export default {
  name: 'schedule',
  data() {
      return {
          scheduleData: false
      }
  },
  components: {
    go_to_game_button
  },
  methods: {
      doStuff() {
          console.log('do stuff');
      },
      getDate(game) {
          return new Date(game.gameDate).toLocaleString("en-US")
      },
      getData(game) {
           return {
               template: Vue.component(go_to_game_button.name, go_to_game_button),
               templateArgs: { gamePk: game.gamePk }
           };
      },
      incrementDate(direction) {
          this.$store.dispatch('incrementDate', direction);
          this.getScheduleData();
      },
      updateDate(evt) {
          this.$store.commit('CHANGE_DATE', evt.sender.value());
          this.getScheduleData();
      },
      getScheduleData() {
          let url = `http://${this.$store.getters.ip}/schedule/${new Date(this.currentDate).toISOString()}`;
          axios.get(url)
          .then(res => {
              this.scheduleData = res.data.dates.pop();
          })
          .catch(err => console.log(err));
      }
  },
  created() {
      this.getScheduleData();
  },
  computed: {
      currentDate() {
          return this.$store.state.currentDate;
      }
  }
}
</script>

<style scoped>
    button {
        margin: 8px;
    }

    .half_screen {
        width:40%;
        display:inline-block;
        border:1px solid black;
        border-radius: 25px;
        text-align: left;
        vertical-align: top;
        padding:25px;
    }
</style>