 <template>
    <div v-show="gameData">
        <h2>{{ gameData.gameData.teams.away.teamName }} @ {{ gameData.gameData.teams.home.teamName }}<br>
            {{ venue }}{{ $store.state.currentDate.toLocaleDateString() }}
        </h2>
        <h4>GamePk #{{ gamePk }}</h4>
        <h4 v-show="m3u8">M3u8 File:<br>{{ m3u8 }}</h4>
        <div>
            <div class="game_data half_screen">
                <pre>{{ JSON.stringify(gameData, null, 2) }}</pre>
            </div>
            <div class="boxscore half_screen">
                <h2>BOXSCORE</h2>
                <linescore :innings="innings" :teams="teams" v-show="gameData.liveData.linescore.innings.length"></linescore>
            </div>
        </div>
    </div>
 </template>
 
 <script>
 import axios from 'axios';
 import linescore from '@/components/Linescore.vue';

 export default {
     data() {
         return {
             gameData: false,
             m3u8: false
         }
     },
     components: {
         linescore
     },
     methods: {
         getGameData() {
            let url = `http://${this.$store.getters.ip}/game/${this.gamePk}`;
            axios.get(url)
            .then(res => {
                console.log(res);
                this.gameData = res.data;
                this.getHLSString();
            })
            .catch(err => console.log(err));
         },
         getHLSString() {
             let url = `http://${this.$store.getters.ip}/assetsearch/${this.gamePk}`;
             axios.get(url)
             .then(res => {
                let asset = res.data.find((assets) => {
                  return (assets.is_lte_master === 1);
                });
                let str = asset.asset_name;
                let year = str.substr(0,4);
                let month = str.substr(4,2);
                let day = str.substr(6,2);
                this.m3u8 = `/${year}/${year}-${month}/${day}/${str}_1280x720_59_3872K.m3u8`;
                console.log('m3u8', this.m3u8);
             })
             .catch(err => console.log('HLS ERROR', err));
         }
     },
     computed: {
         gamePk() {
             return this.$store.state.gamePk;
         },
         venue() {
             if (!this.gameData.liveData.boxscore.info.length) return '';
             return this.gameData.gameData.venue.name + " | ";
         },
         teams() {
             if (!this.gameData) return {};
             let tms = this.gameData.gameData.teams;
             return { away : { name: tms.away.abbreviation, data: this.gameData.liveData.linescore.teams.away }, home: { name: tms.home.abbreviation, data: this.gameData.liveData.linescore.teams.home } };
         },
         innings() {
             if (!this.gameData) return [];
             let ls = this.gameData.liveData.linescore;
             if (!ls) return [];
             let ret = [];
             let scoreDiff = ls.teams.home.runs - ls.teams.away.runs;                         // + = home leads/wins, - = away leads/wins, = = tied
             let gameOver = (scoreDiff) && (ls.currentInning >= ls.scheduledInnings);       // At this point, this means the game COULD be over, not necessarily that it is
             if (gameOver)                                                                  // score is NOT tied & game MAY be over
             {
                 // If game went 9 innings
                 // If home team wins in 8.5, then scoreDiff > 0, ls.isTopInning = true, AND ls.outs = 3
                 // If away team wins in 9th or later, then scoreDiff < 0, ls.isTopInning = false AND ls.outs = 3
                 // If home team wins in walk off (in 9th or later), then scoreDiff > 0, ls.isTopInning = false AND ls.currentInning >= 9
                if (scoreDiff < 0)
                {
                    gameOver = ((!ls.isTopInning) && (ls.outs === 3));
                } else {
                    gameOver = (ls.currentInning > ls.scheduledInnings) ? true : (((ls.isTopInning) && (ls.outs === 3)) || (!ls.isTopInning));
                }
             }

             ls.innings.forEach((inning, $index) => {
                 let runs = { innNum : $index + 1 };
                 if (gameOver)
                 {
                    runs.away = inning.away.runs;
                    runs.home = (inning.home.hasOwnProperty('runs')) ? inning.home.runs : 'x';
                 } else {
                    runs.away = (inning.away.hasOwnProperty('runs')) ? inning.away.runs : null;
                    runs.home = (inning.home.hasOwnProperty('runs')) ? inning.home.runs : null;
                 }
                 ret.push(runs);
             });

             while (ret.length < ls.scheduledInnings) ret.push({ away : null, home : null, innNum: ret.length + 1 });
             return ret;
        }
     },
     created() {
         this.getGameData();
     }
 }
 </script>
 
 <style scoped>
    .half_screen {
        width:40%;
        display:inline-block;
        border:1px solid black;
        border-radius: 25px;
        text-align: left;
        vertical-align: top;
        padding:25px;
    }
 </style>
 