<template>
    <div class="gameData button-container">
        <a href="#" @click="clicked" class="button">
            Go To Game
        </a>
    </div>  
</template>

<script>
import Vue from "vue";

export default {
    name: "go_to_game_button",
    props: ["gamePk"],
    methods: {
        clicked() {
            this.loadGame(this.gamePk);
        }
    },
    mounted() {
        this.gamePk = this._data.templateArgs.gamePk
    }
}
</script>

