<template>
    <div class="linescore">
        <div class="ls_section team_names">
            <div></div>
            <div>{{ teams.away.name }}</div>
            <div>{{ teams.home.name }}</div>
        </div>
        <div class="ls_section inning" v-for="inning in innings" :key="inning.innNum">
            <div class="linescore_header">{{ inning.innNum }}</div>
            <div>{{ inning.away }}</div>
            <div>{{ inning.home }}</div>
        </div>
        <div class="spacer"></div>
        <div class="ls_section tallies" v-for="tally in tallies" :key="tally.header">
            <div class="linescore_header">{{ tally.header }}</div>
            <div>{{ teams.away.data[tally.field] }}</div>
            <div>{{ teams.home.data[tally.field] }}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'linescore',
    props: ['innings', 'teams'],
    data() {
        return {
            tallies: [
                { field: 'runs', header: 'R' },
                { field: 'hits', header: 'H' },
                { field: 'errors', header: 'E' }
            ]
        }
    }
}
</script>

<style scoped>
    .ls_section
    {
        display: inline-block;
        font-size: 24px;
    }

    .ls_section>div
    {
        padding:4px;
    }

    .inning, .tallies
    {
        text-align: center;
        width:40px;
    }

    .spacer
    {
        width:30px;
        display: inline-block;
    }

    .inning:nth-child(3n+4)
    {
        margin-right: 20px;
    }

    .team_names
    {
        width:80px;
    }

    .linescore_header
    {
        font-size: 12px;
    }
</style>
